.contact__container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact__options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;

}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);

}
.contact_option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;

}
.contact__option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;   
}


form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    outline: 2px solid var(--color-bg-variant);
    resize: none;
    caret-color: white;
    color: white;
}


@media screen and (max-width: 1280px){
    .contact__container{
        grid-template-columns: 1fr;
        gap: 2rem;

    }
    .contact__option{
        margin: 0 auto;
        width: 50%;
        background: var(--color-bg-variant);
        padding: 1.2rem 0.5rem;
        border-radius: 1.2rem;
        text-align: center;
        border: 1px solid transparent;
        transition: var(--transition);
    }
    input, textarea{
        width: 80%;
        margin: 0 auto;
        padding: 1.5rem;
        border-radius: 0.5rem;
        background: transparent;
        outline: 2px solid var(--color-bg-variant);
        resize: none;
        caret-color: white;
        color: white;
    
    }
    .send-button{
        margin: 0 auto;
    }
   
}

@media screen and (max-width: 600px){
    .contact__container{
        grid-template-columns: 1fr;
        gap: 2rem;

    }
    .contact__option{
        margin: 0 auto;
        width: 100%;
        background: var(--color-bg-variant);
        padding: 1.2rem 0.5rem;
        border-radius: 1.2rem;
        text-align: center;
        border: 1px solid transparent;
        transition: var(--transition);
    }
    input, textarea{
        width: 100%;
        margin: 0 auto;
        padding: 1.5rem;
        border-radius: 0.5rem;
        background: transparent;
        outline: 2px solid var(--color-bg-variant);
        resize: none;
        caret-color: white;
        color: white;
    }

    .send-button{
        margin: 0 auto;
    }
   
    
}